.slider-block {
	max-width: unset !important;
}

.slideshow-container {
	position: relative;
	background-color: transparent;
}

.slideshow-container .slide {
	padding: 80px;
	text-align: center;
	display: none;
	min-height: 400px;
}

.slideshow-container .prev,
.slideshow-container .next {
	cursor: pointer;
	position: absolute;
	top: 50%;
	width: auto;
	margin-top: -30px;
	padding: 16px;
	color: #888;
	font-weight: bold;
	font-size: 20px;
	border-radius: 0 3px 3px 0;
	user-select: none;
}

.slideshow-container .next {
	position: absolute;
	right: 0;
	border-radius: 3px 0 0 3px;
}

.slideshow-container .prev:hover,
.slideshow-container .next:hover {
	background-color: rgba(0, 0, 0, 0.8);
	color: white;
}

.slideshow-container .navigation-container {
	text-align: center;
	padding: 20px;
}

.slideshow-container .navigation {
	cursor: pointer;
	height: 8px;
	width: 8px;
	margin: 0 3px;
	background-color: #bbb;
	display: inline-block;
	transition: background-color 0.6s ease;
	border-radius: 100%;
}

.slideshow-container .navigation.active {
	height: 8px;
	width: 16px;
	border-radius: 40%;
}

.slideshow-container .navigation.active,
.slideshow-container .navigation:hover {
	background-color: #000000;
}

.slideshow-container a:where(:not(.wp-element-button)) {
	text-decoration: none !important;
}